<template>
  <section class="p-6 max-w-6xl mx-auto">
    <!-- üî• ÏÉÅÎã® ÎûòÌçº: Ìó§ÎçîÎ•º Ìïú Ï§ÑÎ°ú ÏòàÏÅòÍ≤å -->
    <div class="page-inner">
      <!-- Ìó§Îçî -->
      <header class="mb-2 header-row">
        <div class="header-title">
          <h2
            class="page-title text-2xl md:text-3xl font-bold tracking-tight whitespace-nowrap align-middle"
          >
            Ï°∞ÏÇ¨ÏßÄ Ï†úÏ∂ú Î™©Î°ù
          </h2>
        </div>
      </header>

      <!-- Í∏∞Í¥Ä ÏïàÎÇ¥ Ïä§ÎÇµÎ∞î -->
      <div class="snack-area" v-if="showOrgAlert">
        <MaterialSnackbar
          simple
          title="Í∏∞Í¥ÄÏùÑ Î®ºÏ†Ä Îì±Î°ùÌïòÏÑ∏Ïöî."
          description=""
          date=""
          :icon="{ component: 'notifications', color: 'white' }"
          color="danger"
          :closeHandler="closeOrgAlert"
        />
      </div>

      <!-- Ïï°ÏÖò Î≤ÑÌäº ÏòÅÏó≠ -->
      <div class="mb-3 flex justify-between items-center action-row">
        <div class="flex items-center gap-2">
          <!-- üîπ ÏùºÎ∞ò ÏÇ¨Ïö©Ïûê(ROLE=1)Îßå ÎÖ∏Ï∂ú: Ï°∞ÏÇ¨ÏßÄ ÏûëÏÑ±ÌïòÍ∏∞ -->
          <MaterialButton
            v-if="role === 1"
            color="dark"
            size="sm"
            @click="onClickWrite"
          >
            Ï°∞ÏÇ¨ÏßÄ ÏûëÏÑ±ÌïòÍ∏∞
          </MaterialButton>
        </div>
      </div>

      <!-- üîç Í≤ÄÏÉâ / ÌïÑÌÑ∞ / Ï†ïÎ†¨ ÎùºÏù∏ -->
      <div class="filter-row" v-if="role !== 1">
        <form class="filter-form" @submit.prevent="onSearch">
          <!-- Í≤ÄÏÉâ Ïù∏Ìíã -->
          <div class="filter-field filter-field--search">
            <input
              v-model="searchText"
              type="text"
              class="search-input"
              :placeholder="searchPlaceholder"
            />
          </div>

          <!-- ÏÉÅÌÉú ÏÖÄÎ†âÌä∏ -->
          <div class="filter-field filter-field--select select-wrapper">
            <select v-model="statusFilter" class="select-input">
              <option value="ALL">Ï†ÑÏ≤¥ ÏÉÅÌÉú</option>
              <option value="CA1">ÎØ∏Í≤ÄÌÜ†</option>
              <option value="CA3">Í≤ÄÌÜ†ÏôÑÎ£å</option>
            </select>
          </div>

          <!-- Ï†ïÎ†¨ ÏÖÄÎ†âÌä∏ -->
          <div class="filter-field filter-field--select select-wrapper">
            <select v-model="sortOption" class="select-input">
              <option value="RECENT">ÏµúÏã†Ïàú</option>
              <option value="OLD">Ïò§ÎûòÎêúÏàú</option>
              <option value="NAME">Ïù¥Î¶ÑÏàú</option>
            </select>
          </div>

          <!-- Í≤ÄÏÉâ Î≤ÑÌäº -->
          <div class="filter-field filter-field--button">
            <MaterialButton type="submit" color="dark" size="sm">
              Í≤ÄÏÉâ
            </MaterialButton>
          </div>
        </form>
      </div>

      <!-- ÏÉÅÌÉú ÌëúÏãú -->
      <div v-if="loading" class="text-gray-500">Î∂àÎü¨Ïò§Îäî Ï§ë...</div>
      <div v-else-if="error" class="text-red-600">{{ error }}</div>

      <!-- Îã¥ÎãπÏûê(2)Ïù∏Îç∞ Î™©Î°ùÏù¥ ÎπÑÏóàÏùÑ Îïå -->
      <div v-else-if="role === 2 && list.length === 0" class="empty-card">
        ÏïÑÏßÅ Î∞∞Ï†ïÎ∞õÏßÄ ÏïäÏïòÏäµÎãàÎã§.
      </div>

      <!-- üî• ÌÖåÏù¥Î∏î Ïπ¥Îìú -->
      <div v-else class="table-card">
        <table class="nice-table">
          <thead>
            <tr>
              <th class="th-cell text-center w-14">No</th>
              <th v-if="role === 4" class="th-cell">ÏÑ∏Î∂ÄÎ≤ÑÏ†Ñ</th>
              <th class="th-cell">ÏßÄÏõêÏûê Ïù¥Î¶Ñ</th>
              <th class="th-cell">Î≥¥Ìò∏Ïûê Ïù¥Î¶Ñ</th>
              <th class="th-cell">Îã¥ÎãπÏûê Ïù¥Î¶Ñ</th>
              <th v-if="role === 4" class="th-cell">Í∏∞Í¥ÄÎ™Ö</th>
              <th class="th-cell text-center">Ï†úÏ∂úÏùº</th>
              <th class="th-cell text-center">ÏÉÅÌÉú</th>
            </tr>
          </thead>

          <tbody>
            <tr
              v-for="(row, idx) in paginatedList"
              :key="row.submit_code"
              class="table-row-item"
              @click="goToDetail(row.submit_code)"
            >
              <!-- No -->
              <td class="td-cell text-center">
                {{ (currentPage - 1) * pageSize + idx + 1 }}
              </td>

              <!-- ÏãúÏä§ÌÖú(4)Ïùº ÎïåÎßå ÏÑ∏Î∂ÄÎ≤ÑÏ†Ñ -->
              <td v-if="role === 4" class="td-cell text-left mono">
                {{ row.version_detail_no }}
              </td>

              <!-- ÏßÄÏõêÏûê Ïù¥Î¶Ñ -->
              <td class="td-cell text-left">
                {{ row.child_name || "Î≥∏Ïù∏" }}
              </td>

              <!-- Î≥¥Ìò∏Ïûê Ïù¥Î¶Ñ -->
              <td class="td-cell text-left">
                {{ row.writer_name || row.written_by }}
              </td>

              <!-- Îã¥ÎãπÏûê Ïù¥Î¶Ñ -->
              <td class="td-cell text-left">
                {{ row.assignee_name || row.assi_by || "-" }}
              </td>

              <!-- ÏãúÏä§ÌÖú(4)Ïùº ÎïåÎßå Í∏∞Í¥ÄÎ™Ö -->
              <td v-if="role === 4" class="td-cell text-left">
                {{ row.org_name || row.institution_name || "-" }}
              </td>

              <!-- Ï†úÏ∂úÏùº -->
              <td class="td-cell text-center">
                {{ fmt(row.submit_at) }}
              </td>

              <!-- ÏÉÅÌÉú -->
              <td class="td-cell text-center td-status">
                <span class="status-pill" :class="statusClass(row.status)">
                  {{ statusLabel(row.status) }}
                </span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <!-- üîπ ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò -->
      <div v-if="!loading && !error && totalPages > 1" class="mt-6 text-center">
        <MaterialPagination color="dark" size="md" class="pagination">
          <MaterialPaginationItem
            prev
            :disabled="currentPage === 1"
            @click="changePage(currentPage - 1)"
          />
          <MaterialPaginationItem
            v-for="page in totalPages"
            :key="page"
            :label="String(page)"
            :active="page === currentPage"
            @click="changePage(page)"
          />
          <MaterialPaginationItem
            next
            :disabled="currentPage === totalPages"
            @click="changePage(currentPage + 1)"
          />
        </MaterialPagination>
      </div>

      <!-- ÎπÑÏóàÏùÑ Îïå (Îã¥ÎãπÏûê Ï†úÏô∏) -->
      <div
        v-if="!loading && !error && list.length === 0 && role !== 2"
        class="empty-state"
      >
        Ï°∞ÌöåÎêú Ï†úÏ∂úÎ≥∏Ïù¥ ÏóÜÏäµÎãàÎã§.
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import MaterialButton from "@/components/MaterialButton.vue";
import MaterialPagination from "@/components/MaterialPagination.vue";
import MaterialPaginationItem from "@/components/MaterialPaginationItem.vue";
import MaterialSnackbar from "@/components/MaterialSnackbar.vue";

const router = useRouter();

const role = ref(1);

//Î°úÍ∑∏Ïù∏ ÏÇ¨Îûå ÏïÑÏù¥Îîî, Í∏∞Í¥ÄÏΩîÎìú, Í∂åÌïú
const userId = ref(null);
const orgCode = ref(null);
const rawAuthCode = ref("AA1");

const list = ref([]);
const loading = ref(false);
const error = ref("");

const showOrgAlert = ref(false);
const orgAlertTimer = ref(null);

// üîç Í≤ÄÏÉâ / ÌïÑÌÑ∞ / Ï†ïÎ†¨ ÏÉÅÌÉú
const searchText = ref(""); // ÏûÖÎ†•Ï∞ΩÏóê Î≥¥Ïù¥Îäî Í∞í
const appliedSearch = ref(""); // Ïã§Ï†úÎ°ú ÌïÑÌÑ∞Ïóê ÏÇ¨Ïö©ÌïòÎäî Í∞í
const statusFilter = ref("ALL"); // ALL | CA1 | CA3
const sortOption = ref("RECENT"); // RECENT | OLD | NAME

const searchPlaceholder = computed(() => {
  if (role.value === 4) {
    return "ÏßÄÏõêÏûê, Î≥¥Ìò∏Ïûê, Îã¥ÎãπÏûê, Í∏∞Í¥ÄÎ™Ö Í≤ÄÏÉâ";
  }
  return "ÏßÄÏõêÏûê, Î≥¥Ìò∏Ïûê, Îã¥ÎãπÏûê Í≤ÄÏÉâ";
});

// üîπ ÌéòÏù¥Ïßï ÏÉÅÌÉú
const currentPage = ref(1);
const pageSize = 10;

// üîç Í≤ÄÏÉâ / ÏÉÅÌÉúÌïÑÌÑ∞ / Ï†ïÎ†¨ Ï†ÅÏö©Îêú Î¶¨Ïä§Ìä∏
const filteredList = computed(() => {
  let rows = [...list.value];

  // 1) Í≤ÄÏÉâ (Î≤ÑÌäº/ÏóîÌÑ∞ ÎàåÎ†ÄÏùÑ ÎïåÎßå Ï†ÅÏö©ÎêòÎäî appliedSearch ÏÇ¨Ïö©)
  const q = appliedSearch.value;
  if (q) {
    rows = rows.filter((row) => {
      // Í≥µÌÜµ: ÏßÄÏõêÏûê / Î≥¥Ìò∏Ïûê / Îã¥ÎãπÏûê
      const baseTargets = [
        row.child_name, // ÏßÄÏõêÏûê Ïù¥Î¶Ñ
        row.writer_name, // Î≥¥Ìò∏Ïûê Ïù¥Î¶Ñ
        row.assignee_name, // Îã¥ÎãπÏûê Ïù¥Î¶Ñ
      ];

      // ÏãúÏä§ÌÖú Í∂åÌïú(role 4)Ïùº ÎïåÎßå Í∏∞Í¥ÄÎ™Ö Ï∂îÍ∞Ä
      const extraTargets =
        role.value === 4
          ? [row.org_name, row.institution_name] // Í∏∞Í¥ÄÎ™Ö Ïª¨ÎüºÎì§
          : [];

      const targets = [...baseTargets, ...extraTargets];

      return targets.some((v) =>
        String(v || "")
          .toLowerCase()
          .includes(q)
      );
    });
  }

  // 2) ÏÉÅÌÉú ÌïÑÌÑ∞
  if (statusFilter.value !== "ALL") {
    rows = rows.filter((row) => row.status === statusFilter.value);
  }

  // 3) Ï†ïÎ†¨
  if (sortOption.value === "RECENT") {
    // ÏµúÏã† Ï†úÏ∂úÏùº Ïàú (submit_at DESC)
    rows.sort((a, b) => new Date(b.submit_at) - new Date(a.submit_at));
  } else if (sortOption.value === "OLD") {
    // Ïò§ÎûòÎêú Ï†úÏ∂úÏùº Ïàú (submit_at ASC)
    rows.sort((a, b) => new Date(a.submit_at) - new Date(b.submit_at));
  } else if (sortOption.value === "NAME") {
    // ÏßÄÏõêÏûê Ïù¥Î¶Ñ Í∞ÄÎÇòÎã§Ïàú (ÏóÜÏúºÎ©¥ "Î≥∏Ïù∏" Í∏∞Ï§Ä)
    rows.sort((a, b) => {
      const an = a.child_name || "Î≥∏Ïù∏";
      const bn = b.child_name || "Î≥∏Ïù∏";
      return an.localeCompare(bn, "ko");
    });
  }

  return rows;
});

const totalPages = computed(() =>
  Math.max(1, Math.ceil(filteredList.value.length / pageSize) || 1)
);

const paginatedList = computed(() => {
  const start = (currentPage.value - 1) * pageSize;
  return filteredList.value.slice(start, start + pageSize);
});

function changePage(page) {
  if (page < 1 || page > totalPages.value) return;
  currentPage.value = page;
}

function onSearch() {
  // üî• Ïù¥ÎïåÎßå Ïã§Ï†ú Í≤ÄÏÉâÏñ¥ Ï†ÅÏö©
  appliedSearch.value = searchText.value.trim().toLowerCase();
  currentPage.value = 1;
}

/** AA ÏΩîÎìú ‚Üí Ïà´Ïûê Ïó≠Ìï† Îß§Ìïë */
function mapAuthToRole(code) {
  switch (code) {
    case "AA1":
      return 1;
    case "AA2":
      return 2;
    case "AA3":
      return 3;
    case "AA4":
      return 4;
    default:
      return 1;
  }
}

/** Î™©Î°ù Ï°∞Ìöå */
async function fetchList() {
  loading.value = true;
  error.value = "";

  try {
    // ÏùºÎ∞ò ÏÇ¨Ïö©Ïûê(1)Îäî Î∞òÎìúÏãú userId ÌïÑÏöî
    if (role.value === 1 && !userId.value) {
      throw new Error("Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§. (user_code ÏóÜÏùå)");
    }

    const { data } = await axios.get("/api/survey/submissions", {
      params: { role: role.value, userId: userId.value },
    });

    const rows = Array.isArray(data)
      ? data
      : Array.isArray(data?.result)
      ? data.result
      : [];

    list.value = rows;
    currentPage.value = 1; // ÏÉàÎ°ú Ï°∞ÌöåÌï† Îïå Ï≤´ ÌéòÏù¥ÏßÄÎ°ú
  } catch (e) {
    error.value =
      e?.response?.data?.message || e.message || "Î™©Î°ùÏùÑ Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.";
    list.value = [];
  } finally {
    loading.value = false;
  }
}

/** ÎÇ†Ïßú Ìè¨Îß∑ */
function fmt(v) {
  if (!v) return "-";
  const d = new Date(v);
  return isNaN(d) ? String(v) : d.toISOString().slice(0, 10);
}

/** ÏÉÅÌÉú ÎùºÎ≤® */
function statusLabel(code) {
  switch (code) {
    case "CA1":
      return "ÎØ∏Í≤ÄÌÜ†";
    case "CA3":
      return "Í≤ÄÌÜ†ÏôÑÎ£å";
    default:
      return code || "-";
  }
}

function statusClass(code) {
  switch (code) {
    case "CA1": // ÎØ∏Í≤ÄÌÜ†
      return "status-pending";
    case "CA3": // Í≤ÄÌÜ†ÏôÑÎ£å
      return "status-done";
    default:
      return "status-default";
  }
}

/** ÏÉÅÏÑ∏ ÌéòÏù¥ÏßÄ Ïù¥Îèô */
function goToDetail(submitCode) {
  router.push({
    path: `/survey/submission/${submitCode}`,
    query: { role: role.value, userId: userId.value },
  });
}

// Ï°∞ÏÇ¨ÏßÄ ÏûëÏÑ±ÌïòÍ∏∞
function onClickWrite() {
  if (!orgCode.value) {
    // Ïù¥ÎØ∏ Îñ† ÏûàÎçò ÌÉÄÏù¥Î®∏ ÏûàÏúºÎ©¥ Ï†ïÎ¶¨
    if (orgAlertTimer.value) {
      clearTimeout(orgAlertTimer.value);
      orgAlertTimer.value = null;
    }

    showOrgAlert.value = true;

    // 2.5Ï¥à ÌõÑ ÏûêÎèôÏúºÎ°ú Îã´ÌûàÍ≤å
    orgAlertTimer.value = setTimeout(() => {
      showOrgAlert.value = false;
      orgAlertTimer.value = null;
    }, 2500);

    return;
  }
  router.push("/survey/write");
}

// Ïä§ÎÇµÎ∞î X Î≤ÑÌäº ÎàåÎ†ÄÏùÑ Îïå Îã´Í∏∞
function closeOrgAlert() {
  showOrgAlert.value = false;
  if (orgAlertTimer.value) {
    clearTimeout(orgAlertTimer.value);
    orgAlertTimer.value = null;
  }
}

//localStorageÏóêÏÑú Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥ ÏùΩÍ∏∞
onMounted(() => {
  try {
    const stored = localStorage.getItem("user");

    if (stored) {
      const u = JSON.parse(stored);

      const userCode = u.user_code ?? u.userCode ?? u.id;
      const auth = u.auth_code ?? u.authCode ?? u.role_code ?? u.role ?? "AA1";
      const org = u.org_code ?? u.orgCode ?? null;

      userId.value = userCode ? Number(userCode) : null;
      rawAuthCode.value = auth;
      role.value = mapAuthToRole(String(auth).toUpperCase());
      orgCode.value = org;
    } else {
      userId.value = null;
      rawAuthCode.value = "AA1";
      role.value = 1;
      orgCode.value = null;
    }
  } catch (e) {
    console.error("localStorage ÌååÏã± Ïò§Î•ò:", e);
    userId.value = null;
    rawAuthCode.value = "AA1";
    role.value = 1;
    orgCode.value = null;
  }

  fetchList();
});
</script>

<style scoped>
section {
  color: #111827;
  font-size: 15px;
}

/* ÏïàÏ™Ω ÎûòÌçº: Ï†ÑÏ≤¥ Ìè≠ Ï†úÌïú + ÏÇ¥Ïßù Í∞ÑÍ≤© */
.page-inner {
  max-width: 1600px;
  margin: 0 auto;
}

/* Ìó§Îçî Ìïú Ï§Ñ Ïú†ÏßÄ */
.header-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  gap: 1rem;
}

/* Ï†úÎ™© Ï™Ω: ÌïÑÏöîÌïòÎ©¥ Ï§Ñ ÏûòÎ¶¨Í≤å */
.header-title {
  flex: 1 1 auto;
  min-width: 0;
}

.page-title {
  letter-spacing: -0.02em;
}

/* Ïò§Î•∏Ï™Ω Ïó≠Ìï† Ï†ïÎ≥¥ */
.header-meta {
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

/* Ïï°ÏÖò ÎùºÏù∏ */
.action-row {
  margin-top: 0.25rem;
}

/* Îã¥ÎãπÏûê empty Ïπ¥Îìú */
.empty-card {
  border-radius: 0.75rem;
  border: 1px dashed #d1d5db;
  background-color: #f9fafb;
  padding: 1.75rem 1.25rem;
  color: #4b5563;
}

/* Í≥µÌÜµ ÌÖåÏù¥Î∏î Ïπ¥Îìú */
.table-card {
  border-radius: 0.9rem;
  border: 1px solid #e5e7eb;
  background-color: #ffffff;
  box-shadow: 0 10px 24px rgba(15, 23, 42, 0.06);
  overflow: hidden;
  margin-top: 0.5rem;
}

/* ÌÖåÏù¥Î∏î Í∏∞Î≥∏ */
.nice-table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
}

/* Ìó§Îçî ÏÖÄ */
.th-cell {
  padding: 0.7rem 0.9rem;
  text-align: center;
  font-weight: 600;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #6b7280;
  background-color: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
  white-space: nowrap;
}

@media (max-width: 900px) {
  .th-cell {
    white-space: normal; /* Ï§ÑÎ∞îÍøà ÌóàÏö© */
    font-size: 13px;
    line-height: 1.3;
    padding: 0.4rem 0.5rem;
  }
}

/* Î∞îÎîî ÏÖÄ */
.td-cell {
  padding: 0.65rem 0.9rem;
  border-bottom: 1px solid #f3f4f6;
  color: #111827;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.td-cell.mono {
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas,
    "Liberation Mono", "Courier New", monospace;
  color: #4b5563;
}

/* Ìñâ Ïä§ÌÉÄÏùº */
.table-row-item {
  transition:
    background-color 0.12s ease,
    box-shadow 0.15s ease,
    transform 0.08s ease;
  cursor: pointer;
}

/* Ï§ÑÎ¨¥Îä¨ ÎäêÎÇå (Î¨ΩÍ≤å) */
.table-row-item:nth-child(odd) {
  background-color: #ffffff;
}
.table-row-item:nth-child(even) {
  background-color: #f9fafb;
}

/* hover Ìö®Í≥º */
.table-row-item:hover {
  background-color: #f3f4f6;
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(15, 23, 42, 0.08);
}

/* ÎØ∏Í≤ÄÌÜ†(CA1) ‚Üí Gray pastel */
.status-pending {
  background-color: #e4e6e1 !important; /* p-bg-gray */
  border-color: #d0d3cd !important; /* p-border-gray */
  color: #4b5563 !important; /* p-text-gray */
}

/* Í≤ÄÌÜ†ÏôÑÎ£å(CA3) ‚Üí Green pastel */
.status-done {
  background-color: #deeec8 !important; /* p-bg-green */
  border-color: #bedca0 !important; /* p-border-green */
  color: #3f7a3a !important; /* p-text-green */
}

/* Í∏∞ÌÉÄ ÏΩîÎìúÏö© Í∏∞Î≥∏ */
.status-default {
  background-color: #f2f3f5 !important;
  border-color: #d6d9df !important;
  color: #646b78 !important;
}

/* Í≥µÌÜµ Ìè∞Ìä∏ Ï†ïÎ¶¨ (Ìó§Îçî/Î∞îÎîî Í∞ôÏù¥) */
table th,
table td {
  font-family:
    "Noto Sans KR",
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    sans-serif;
  text-align: center;
}

/* ÎπÑÏóàÏùÑ Îïå (Îã¥ÎãπÏûê Ï†úÏô∏) */
.empty-state {
  text-align: center;
  padding: 3rem 1rem;
  color: #9ca3af;
}

/* ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò */
.pagination {
  display: inline-flex;
}

.td-status {
  overflow: visible;
  text-overflow: clip;
  white-space: nowrap;
}

/* üîç ÌïÑÌÑ∞ ÎùºÏù∏ */
.filter-row {
  margin-bottom: 0.75rem;
  margin-top: 0.25rem;
  width: 100%;
}

